<template>
  <div class="unit-box">
    <div class="banner-box" :style="{'background-image': `url('${publicPath}image/news_1.png')`}">
       <div class="t-1">{{ info.name }}</div>
       <div class="t-2">{{ info.title }}</div>
    </div>
    <div class="unit-content">
      <ul class="list-box">
        <li v-for="(item, j) in info.children" :key="j" class="list-item" @click="goLink('/answering/' + item.id)">
          <img :src="unitImg" alt="">
          <h2>{{ item.name }}</h2>
          <!-- <p v-if="j % 2 === 1">{{ info.title }}</p> -->
          <div class="btn-box">
            <div v-if="item.permission === 1">User Only</div>
            <div v-else class="active">Public</div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { getUnit } from '@/api'
import unitImg from '@/assets/unit_1.png'
export default {
  props: {
    unit_id: {
      type: [String, Number],
      default: () => ''
    }
  },
  data() {
    return {
      publicPath: process.env.BASE_URL,
      info: {},
      unitImg
    }
  },
  watch: {
    unit_id: {
      handler() {
        this.getData()
      },
      immediate: true
    }
  },
  methods: {
    goLink(path) {
      this.$router.push({ path })
    },
    getData() {
      getUnit({ id: this.unit_id }).then(res => {
        this.info = res
      }).catch(() => {})
    }
  }
}
</script>
